import Commerce from '@chec/commerce.js';
import { useEffect } from 'react';
import { useAppDispatch } from '../app/hooks';
import { setCart } from '../features/cart/cart';
import { productsFetched } from '../features/products/products';
import { Cart, Product } from '../types';

const commerce = new Commerce(process.env.REACT_APP_CHEC_PUBLIC_KEY, true);
// console.log(commerce);

export const useCommerceStore = (): void => {
	console.log('useCommerceStore()');
	const dispatch = useAppDispatch();
	useEffect(() => {
		console.log('useCommerceStore fetching');

		const fetchData = async () => {
			try {
				const { data: productsData = [] }: { data: Product[] } = await commerce.products.list();
				const cartData = await commerce.cart.retrieve();
				dispatch(productsFetched(productsData));
				dispatch(setCart(cartData));
			} catch {
				console.error('fetch from commerce failed');
			}
		};
		fetchData();
	}, [dispatch]);
};

export const addToCart = async (productId: string, quantity = 1): Promise<Cart> => {
	const { cart } = await commerce.cart.add(productId, quantity);
	return cart;
};
export const updateCartQty = async (productId: string, quantity: number): Promise<Cart> => {
	const { cart } = await commerce.cart.update(productId, { quantity });
	return cart;
};
export const removeFromCart = async (productId: string): Promise<Cart> => {
	const { cart } = await commerce.cart.remove(productId);
	return cart;
};
export const emptyCart = async (): Promise<Cart> => {
	const { cart } = await commerce.cart.empty();
	return cart;
};
