import { FC } from 'react';
import { Link } from 'react-router-dom';
import { useAppSelector } from '../../../app/hooks';

import { ReactComponent as CartIco } from '../../../assets/cart.svg';

import './buttons.css';

const Cart: FC = () => {
	const { total_unique_items: TotalUniqueItems, subtotal } = useAppSelector((state) => state.cart);
	return (
		<div className="cart nav__cart popup-catch">
			<Link to="/cart">
				<CartIco className="cart__ico popup-receive" />
				<div className="cart__counter">{TotalUniqueItems || 0}</div>
				<div className="cart__price">{`${subtotal.formatted || (0.0).toFixed(2)} zł`}</div>
			</Link>
		</div>
	);
};

export default Cart;
