/* eslint-disable react/jsx-props-no-spreading */
import classNames from 'classnames';
import { FC, useState } from 'react';

import './input.css';

interface Props {
	type: string;
	id: string;
	label: string;
	containerClass?: string;
	defaultInput?: string;
}
type UseStateArr = [string, React.Dispatch<React.SetStateAction<string>>];
type InputChoiceProps = Props & { valueSetValue?: UseStateArr };
type ExternalControlledProps = Props & { valueSetValue: UseStateArr };

const InputHtml: FC<ExternalControlledProps> = ({
	id,
	label,
	type,
	valueSetValue,
	containerClass
}) => {
	const [value, setValue] = valueSetValue;
	return (
		<div className={classNames('input-container popup', containerClass, { filled: value })}>
			<input
				type={type}
				id={id}
				className="input"
				value={value}
				onChange={(e) => setValue(e.target.value)}
			/>
			<label htmlFor={id}>{label}</label>
		</div>
	);
};
const ControlledInput: FC<Props> = (props) => {
	// eslint-disable-next-line react/destructuring-assignment
	const valueSetValue = useState(props.defaultInput || '');
	return <InputHtml {...props} valueSetValue={valueSetValue} />;
};
const InputChoice: FC<InputChoiceProps> = (props) => {
	// eslint-disable-next-line react/destructuring-assignment
	return props.valueSetValue ? (
		<InputHtml {...(props as ExternalControlledProps)} />
	) : (
		<ControlledInput {...props} />
	);
};
export default InputChoice;
