import { FC, SetStateAction, useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { Product } from '../../../types';
import { addToCart, updateCartQty } from '../../../api/commerce';
import { AppDispatch } from '../../../app/store';
import { useAppDispatch, useAppSelector } from '../../../app/hooks';
import { setCart } from '../../../features/cart/cart';
import Input from '../input/input';

interface Props {
	product: Product;
}
const ProductCard: FC<Props> = ({
	product: {
		name,
		id,
		media: { source },
		price: { raw: price, formatted }
	}
}) => {
	const dispatch = useAppDispatch();

	const handleClick = async () => {};
	return (
		<div className="product popup-catch">
			<h1 className="popup-receive">{name}</h1>
			<div className="product__content">
				<img
					src={source}
					alt={name}
					width="169"
					height="177"
					className="product__img popup-receive"
				/>
				<aside>
					{/* eslint-disable-next-line prettier/prettier */}
					<div className="product__price">{`${formatted} zł`}</div>
					<button
						type="button"
						className="btn product__btn btn--green popup popup-receive"
						onClick={handleClick}>
						Do koszyka
					</button>
				</aside>
			</div>
		</div>
	);
};

export default ProductCard;
