import { CSSProperties } from 'react';

export const calcDelay = (
	staggerStep: number,
	nodeIndex: number,
	nodesTotal: number,
	delay: number,
	reverse = false,
	skipTransition = false
): CSSProperties => {
	return {
		transitionDelay: `${
			skipTransition ? 0 : delay + (reverse ? nodesTotal - nodeIndex - 1 : nodeIndex) * staggerStep
		}s`,

		animationDelay: `${nodeIndex * staggerStep}s`
	};
};
