import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Cart } from '../../types';

const initialState: Cart = {
	id: null,
	created: null,
	updated: null,
	expires: null,
	total_items: 0,
	total_unique_items: 0,
	subtotal: {
		raw: 0,
		formatted: '0.00',
		formatted_with_symbol: 'zł0.00',
		formatted_with_code: '0.00 PLN'
	},
	line_items: [],
	currency: {
		code: 'PLN',
		symbol: 'zł'
	},
	discount: [],
	hosted_checkout_url: ''
};

const productsSlice = createSlice({
	name: 'cart',
	initialState,
	reducers: {
		setCart(_, action: PayloadAction<Cart>) {
			return action.payload;
		},
		// softUpdateCart(state, action: PayloadAction<{ isUnique: boolean; price: number }>) {
		// 	const { isUnique, price } = action.payload;
		// 	state.total_items++;
		// 	state.subtotal.raw += price;
		// 	const { raw: newPrice } = state.subtotal;
		// 	state.subtotal.formatted = newPrice.toLocaleString('en');
		// 	const { formatted: newFormatted } = state.subtotal;
		// 	state.subtotal.formatted_with_code = `${newFormatted} PLN`;
		// 	state.subtotal.formatted_with_symbol = `zł${newFormatted}`;
		// 	if (isUnique) state.total_unique_items++;
		// }
	}
});

export const { setCart } = productsSlice.actions;
export default productsSlice.reducer;
