import { FC, ReactChild } from 'react';
import classNames from 'classnames';
import './blur.css';

interface Props {
	children: ReactChild;
	parentClass?: string;
}

const BackgroundBlur: FC<Props> = ({ children: child, parentClass }) => {
	return (
		<div className={classNames('wrapper', parentClass && `${parentClass}__wrapper`)}>
			<div
				className={classNames(
					'wrapper__blur-background',
					parentClass && `${parentClass}__blur-background`
				)}
			/>
			{child}
		</div>
	);
};

export default BackgroundBlur;
